@import url(https://fonts.googleapis.com/css?family=Roboto:900);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.SingleChoice-Wrapper{margin:20px;border:1px solid black;padding:20px}.SingleChoice-Wrapper_isRed{border:3px solid red}.SingleChoice-QuestionTitle{font-size:20px;font-weight:bold;display:inline-block}.SingleChoice-Description{margin:20px 0}.SingleChoice-AnswersWrapper{display:flex;flex-direction:column}.SingleChoice-AnswersWrapper_isImage{flex-direction:row;grid-gap:20px;gap:20px}.SingleChoice-Pts{display:inline-block;float:right}.SingleChoice-Answer img{padding:5px;width:200px;height:200px}.SingleChoice-Answer_isRed{background-color:red}.SingleChoice-Answer_isGreen{background-color:green}.SingleChoice-Answer_isImage input{display:none}.SingleChoice-Answer_isImage .SingleChoice-Label{border:2px solid transparent}.SingleChoice-Answer_isImage .SingleChoice-Label_isSelected{border:2px solid purple}

.MultiChoice-Wrapper{margin:20px;border:1px solid black;padding:20px}.MultiChoice-Wrapper_isRed{border:3px solid red}.MultiChoice-QuestionTitle{font-size:20px;font-weight:bold;display:inline-block}.MultiChoice-Description{margin:20px 0}.MultiChoice-AnswersWrapper{display:flex;flex-direction:column}.MultiChoice-AnswersWrapper_isImage{flex-direction:row;grid-gap:20px;gap:20px}.MultiChoice-Pts{display:inline-block;float:right}.MultiChoice-Answer img{padding:5px;width:200px;height:150px}.MultiChoice-Answer_isRed{background-color:red}.MultiChoice-Answer_isGreen{background-color:green}.MultiChoice-Answer_isImage input{display:none}.MultiChoice-Answer_isImage .MultiChoice-Label{border:2px solid transparent}.MultiChoice-Answer_isImage .MultiChoice-Label_isSelected{border:2px solid purple}

.Text-Wrapper{margin:20px;border:1px solid black;padding:20px}.Text-Wrapper_isRed{border:3px solid red}.Text-QuestionTitle{font-size:20px;font-weight:bold;display:inline-block}.Text-Description{margin:20px 0}.Text-Pts{display:inline-block;float:right}.Text-AnswersWrapper{display:flex;flex-direction:column}.Text-AnswersWrapper_isRed{background-color:red}.Text-AnswersWrapper_isGreen{background-color:green}

.Quiz-Message,.Quiz-Timer{margin-top:5px;text-align:center;width:100%;font-size:30px;color:black}.Quiz-Button{display:inline-block;cursor:pointer;margin:auto;display:flex;text-align:center;padding:0.3em 1.2em;border-radius:2em;margin-bottom:50px;box-sizing:border-box;text-decoration:none;font-family:'Roboto', sans-serif;color:#FFFFFF;font-size:20px;background-color:#4eb5f1;transition:all 0.2s}.Quiz-Button:hover{background-color:#4095c6}@media all and (max-width: 30em){.Quiz-Button{display:block;margin:0.2em auto}}

*{box-sizing:border-box}*::before,*::after{box-sizing:border-box}.App-Introduction{font-size:25px;text-align:center;margin-top:50px}body{font-family:'Roboto', sans-serif;font-size:1rem;line-height:1.5;display:flex;align-items:center;justify-content:center;margin:0;min-height:100vh;background:#fff}.App-StartButton{display:inline-block;cursor:pointer;padding:0.3em 1.2em;margin:0 0.3em 0.3em 0;border-radius:2em;box-sizing:border-box;text-decoration:none;font-family:'Roboto',sans-serif;color:#FFFFFF;font-size:20px;background-color:#4eb5f1;text-align:center;transition:all 0.2s}.App-StartButton:hover{background-color:#4095c6}@media all and (max-width: 30em){.App-StartButton{display:block;margin:0.2em auto}}

